export const debounce = (fn, delay = 150, thisArg) => {
	let timer;
	return (...args) => {
		if (timer) clearTimeout(timer);
		timer = setTimeout(() => thisArg ? fn.call(thisArg, ...args) : fn(...args), delay);
	};
}

export const pinyin2num = txt => {
	return txt.split(' ').map(e => {
		if (/[]/[Symbol.match](e)) {
			return e.replace(/[]/, i => ({ '': 'a', '': 'o', '': 'e', '': 'i', '': 'u', '': 'v' })[i]) + '1';
		} else if (/[]/[Symbol.match](e)) {
			return e.replace(/[]/, i => ({ '': 'a', '': 'o', '': 'e', '': 'n', '': 'i', '': 'u', '': 'v', '': 'm' })[i]) + '2';
		} else if (/[]/[Symbol.match](e)) {
			return e.replace(/[]/, i => ({ '': 'a', '': 'o', '': 'e', '': 'n', '': 'i', '': 'u', '': 'v' })[i]) + '3';
		} else if (/[]/[Symbol.match](e)) {
			return e.replace(/[]/, i => ({ '': 'a', '': 'o', '': 'e', '': 'n', '': 'i', '': 'u', '': 'v' })[i]) + '4';
		} else {
			return e;
		}
	}).join(' ');
}